<template lang="en">
<section>
    <loader :is-visible="isLoading" />
    <div>
        <div style="float:right">
            <button class="btn btn-primary w-100" @click="EditDetails()">
                {{this.$t('Edit Details')}}
            </button>
        </div>
        <div>
            <dynamic-form lang="en" ref="AccountInformation" :buttons="buttons" :schema="FormSchema" :mandatorytext="$t('MandatoryString')" />
        </div>
    </div>
    <div>
        <EditAccountInfo v-if="IsEditDetail" :AccountInfoData="AccountInfoData"></EditAccountInfo>
    </div>
</section>
</template>

<script>
import DataService from '../../services/DataService';
import EditAccountInfo from '../../components/Modals/EditAccountInfoDetail.vue'
export default {
    components: {
        EditAccountInfo
    },
    props: {

    },
    data() {
        return {
            isLoading: false,
            AccountInfoData: [],
            IsEditDetail: false,
            FormSchema: [{
                    group_name: "User Details",
                    layoutType: "four",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('AccountNumber'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('AccountNumber'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Company'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Company'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Email'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Email'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('FirstName'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('LastName'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Phone'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Phone'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                    ],
                },
                {
                    group_name: "Billing Address",
                    layoutType: "four",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('StreetAddress'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('StreetAddress'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Country'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Country'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('City'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('State'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('State'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('ZipCode'),
                            validationRules: {
                                required: true
                            },
                            disabled: true,
                        },
                    ]
                },

            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseCategory,
                },
            ],
        }
    },
    async created() {
        debugger;
        await this.DisplayBillingInfo();
    },
    methods: {
        async DisplayBillingInfo() {
            await DataService.DisplayBillingInfo().then(res => {
                debugger
                if (res.data.result.length > 0) {
                    const AccountInfoData = JSON.parse(res.data.result);
                    this.AccountInfoData = AccountInfoData;
                    this.FormSchema[0].Data[0].value = AccountInfoData.CompanyId;
                    this.FormSchema[0].Data[1].value = AccountInfoData.CompanyName;
                    this.FormSchema[0].Data[2].value = AccountInfoData.Email;
                    this.FormSchema[0].Data[3].value = AccountInfoData.bFirstName;
                    this.FormSchema[0].Data[4].value = AccountInfoData.bLastName;
                    this.FormSchema[0].Data[5].value = AccountInfoData.bPhone;
                    this.FormSchema[1].Data[0].value = AccountInfoData.bAddress1;
                    this.FormSchema[1].Data[1].value = AccountInfoData.bCountry;
                    this.FormSchema[1].Data[2].value = AccountInfoData.bCity;
                    this.FormSchema[1].Data[3].value = AccountInfoData.bState;
                    this.FormSchema[1].Data[4].value = AccountInfoData.bZipCode;
                }
            })
        },
        EditDetails() {
            this.IsEditDetail = true;
        },

    }
}
</script>

<style lang="scss" scoped></style>
