<template lang="en">
<section>
    <div class="my-popups">
        <loader :is-visible="isLoading" />

        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                        <h5 class="modal-title">{{ FormLabel }}</h5>
                        <button type="button" class="close" @click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" ref="validateSubsidy" :buttons="buttons" :schema="FormSchema" :mandatorytext="$t('MandatoryString')" />
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import DataService from '../../services/DataService';

export default {
    props: {
        AccountInfoData: {
            type: Array,
            required: true,
        },
    },
    data() {
        return {
            isLoading: false,
            FormLabel: 'Add Subsidy',
            FormSchema: [{
                    layoutType: "four",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('AccountNumber'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('AccountNumber'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Company'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Company'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Email'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Email'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('FirstName'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('LastName'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Phone'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Phone'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('StreetAddress'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('StreetAddress'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('StreetAddress2'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('StreetAddress2'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Country'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('Country'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('City'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('State'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('State'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "category",
                            value: "",
                            placeholder: this.$t('ZipCode'),
                            validationRules: {
                                required: false
                            },
                            disabled: false,
                        },
                    ]
                },

            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveSubsidyData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.Close,
                },
            ],
        };
    },

    created: async function () {
        debugger
        var vm = this;
        if (vm.AccountInfoData.CompanyId>0) {
            vm.FormSchema[0].Data[0].value = vm.AccountInfoData.CompanyId;
            vm.FormSchema[0].Data[1].value = vm.AccountInfoData.CompanyName;
            vm.FormSchema[0].Data[2].value = vm.AccountInfoData.Email;
            vm.FormSchema[0].Data[3].value = vm.AccountInfoData.bFirstName;
            vm.FormSchema[0].Data[4].value = vm.AccountInfoData.bLastName;
            vm.FormSchema[0].Data[5].value = vm.AccountInfoData.bPhone;
            vm.FormSchema[0].Data[6].value = vm.AccountInfoData.bAddress1;
            vm.FormSchema[1].Data[1].value = vm.AccountInfoData.bCountry;
            vm.FormSchema[1].Data[2].value = vm.AccountInfoData.bCity;
            vm.FormSchema[1].Data[3].value = vm.AccountInfoData.bState;
            vm.FormSchema[1].Data[4].value = vm.AccountInfoData.bZipCode;
        }

    },

    methods: {
        BindSubsidyDetail: function () {

            this.$refs.validateSubsidy.updateProperty("SubsidyName", "value", this.EditData[0].Subsidy_Name);
            this.$refs.validateSubsidy.updateProperty("SubsidyType", "value", this.EditData[0].Subsidy_Type);
            this.$refs.validateSubsidy.updateProperty("CustomerType", "value", this.EditData[0].Category);
            this.$refs.validateSubsidy.updateProperty("Value", "value", this.EditData[0].Subsidy_Value);
            this.$refs.validateSubsidy.updateProperty("Effectivefrom", "value", this.EditData[0].Effective_From);
            this.$refs.validateSubsidy.updateProperty("Effectiveto", "value", this.EditData[0].Effective_To);
            this.$refs.validateSubsidy.updateProperty("Description", "value", this.EditData[0].Subsidy_Description);

        },
        Close() {
            this.$parent.IsEditDetail = false;
        },

        GetDDLCustomerTypeData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=CUSTOMER_TYPE&search=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateSubsidy.updateProperty("CustomerType", "config.options", response.data.DATA);
                        vm.$refs.validateSubsidy.UpdateKeyValue();
                    }
                }
            });
        },

        async SaveSubsidyData() {

            var vm = this;
            const result = await vm.$refs.validateSubsidy.validateForm.validate();
            if (result) {
                var SubsidyName = vm.$refs.validateSubsidy.getPropertyValue("SubsidyName", "value");
                var SubsidyType = vm.$refs.validateSubsidy.getPropertyValue("SubsidyType", "value");
                var CustomerType = vm.$refs.validateSubsidy.getPropertyValue("CustomerType", "value");
                var Value = vm.$refs.validateSubsidy.getPropertyValue("Value", "value");
                var EffectiveFrom = vm.$refs.validateSubsidy.getPropertyValue("Effectivefrom", "value");
                var EffectiveTo = vm.$refs.validateSubsidy.getPropertyValue("Effectiveto", "value");
                var Description = vm.$refs.validateSubsidy.getPropertyValue("Description", "value");
                var UpdateMapped = vm.$refs.validateSubsidy.getPropertyValue("UpdatePlanMapped", "value");

                var objData = {
                    Id: Array.isArray(vm.EditData) && vm.EditData.length > 0 ? vm.EditData[0].Id : 0,
                    SubsidyName: SubsidyName,
                    SubsidyType: SubsidyType,
                    CustomerType: CustomerType,
                    Value: Value,
                    EffectiveFrom: EffectiveFrom,
                    EffectiveTo: EffectiveTo,
                    Description: Description,
                    UpdateMapped: UpdateMapped

                }

                var obj = JSON.stringify(objData);

                await DataService.AddSubsidyData(obj).then(response => {
                    if (response.data != null) {
                        if (response.data[0] == "Success") {
                            vm.ShowAlert(vm.$t("DataSavedSuccessfully"), "success", vm.$t('Alert'));
                            vm.CloseSubsidy();
                            vm.$parent.GetSubsidyData();

                            vm.isLoading = false;
                        } else {

                            vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
                            vm.isLoading = false;
                        }
                    }
                });

            }
        },

    }
};
</script>
